@font-face {
  font-display: swap;
  font-family: "Inter-Regular";
  src: url("../fonts/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter-Regular.ttf") format("truetype"), url("../fonts/Inter-Regular.otf") format("opentype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-display: swap;
  font-family: "Inter-SemiBold";
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2"), url("../fonts/Inter-SemiBold.ttf") format("truetype"), url("../fonts/Inter-SemiBold.otf") format("opentype");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-display: swap;
  font-family: "Caveat-Bold";
  src: url("../fonts/Caveat-SemiBold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}
body, html {
  background: var(--white);
  color: var(--black);
  font-family: var(--font-family);
  font-size: var(--c-font-size);
  line-height: 1.4;
  font-weight: 400;
  margin: 0 auto;
  -webkit-text-size-adjust: 100%;
}

body {
  font-size: var(--c-font-size);
  letter-spacing: 0.45px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1:first-child, h2:first-child, h3:first-child, h4:first-child, h5:first-child, h6:first-child, p:first-child, ul:first-child, ol:first-child, li:first-child, blockquote:first-child, tt:first-child, mark:first-child,
form:first-child, table:first-child, a:first-child, .button:first-child {
  margin-top: 0 !important;
}
h1:last-child, h2:last-child, h3:last-child, h4:last-child, h5:last-child, h6:last-child, p:last-child, ul:last-child, ol:last-child, li:last-child, blockquote:last-child, tt:last-child, mark:last-child,
form:last-child, table:last-child, a:last-child, .button:last-child {
  margin-bottom: 0 !important;
}

a {
  text-decoration: none;
  transition: all ease-in-out 0.2s;
}

html {
  --c-padding: 0;
  --c-font-size: 14px;
  --cols: 12;
  --gutter: 20px;
  --column: calc((100% - 20px * (12 - 1)) / 12);
  --w-content: 960px;
  --h-header: 48px;
  --font-family: "Inter-Regular", sans-serif;
  --font-family-b: "Inter-SemiBold", sans-serif;
  --font-family-c: "Caveat-Bold", sans-serif;
  --desctop-h1h2-font-size: 52px;
  --desctop-h1h2-line-height: 116%;
  --desctop-h1h2-font-weight: 600;
  --desctop-h3-font-size: 32px;
  --desctop-h3-line-height: 36px;
  --desctop-h3-font-weight: 600;
  --desctop-body-s-sb-font-size: 13px;
  --desctop-body-s-sb-line-height: 140%;
  --desctop-body-s-sb-font-weight: 600;
  --desctop-body-s-reg-font-size: 13px;
  --desctop-body-s-reg-line-height: 140%;
  --desctop-body-s-reg-font-weight: 400;
  --desctop-body-m-sb-font-size: 17px;
  --desctop-body-m-sb-line-height: 22px;
  --desctop-body-m-sb-font-weight: 600;
  --desctop-body-l-sb-font-size: 22px;
  --desctop-body-l-sb-line-height: 28px;
  --desctop-body-l-sb-font-weight: 600;
  --desctop-cta-s-font-size: 13px;
  --desctop-cta-s-line-height: 120%;
  --desctop-cta-s-font-weight: 400;
  --desctop-cta-s-link-font-size: 13px;
  --desctop-cta-s-link-line-height: 120%;
  --desctop-cta-s-link-font-weight: 400;
  --desctop-sta-m-font-size: 15px;
  --desctop-sta-m-line-height: 120%;
  --desctop-sta-m-font-weight: 400;
  --desctop-cta-m-link-font-size: 15px;
  --desctop-cta-m-link-line-height: 120%;
  --desctop-cta-m-link-font-weight: 400;
  --desctop-textaccedent-font-size: 24px;
  --desctop-textaccedent-line-height: 25px;
  --desctop-textaccedent-font-weight: 700;
  --desctop-list-font-size: 13px;
  --desctop-list-line-height: 120%;
  --desctop-list-font-weight: 400;
  --mob-h1h2-font-size: 38px;
  --mob-h1h2-line-height: 42px;
  --mob-h1h2-font-weight: 600;
  --mob-h3-font-size: 26px;
  --mob-h3-line-height: 30px;
  --mob-h3-font-weight: 600;
  --mob-cta-m-font-size: 15px;
  --mob-cta-m-line-height: 20px;
  --mob-cta-m-font-weight: 400;
  --mob-cta-m-semibold-font-size: 15px;
  --mob-cta-m-semibold-line-height: 20px;
  --mob-cta-m-semibold-font-weight: 600;
  --white: #fff;
  --black: #282827;
  --gray: #747473;
  --gray-2: #929292;
  --gray-3: #d4d4d4;
  --line-light: #f5f5f5;
  --bg-light: #fafafa;
  --bg-light-2: #f9f9f9;
  --yellow-main: #f8ca02;
  --yellow-secondary: #ffdf65;
  --orange: #ffc165;
  --orange-2: #f1711c;
  --green: #56e264;
  --pro-grey-pro: #a8a8a8;
  --pro-pro-lines: #232322;
  --pro-lines-bg-pro: #151515;
  --successful: #49aa53;
  --violet: #7165ff;
  --error: #e64832;
  --toner: rgba(0, 0, 0, 0.25);
  --toner-160: rgba(0, 0, 0, 0.16);
}
@media (max-width: 640px) {
  html {
    --c-padding: 20px;
  }
}

.overflow {
  overflow: hidden;
}

.hidden {
  display: none !important;
}
@media (max-width: 640px) {
  .hidden--mob {
    display: none !important;
  }
}
@media (min-width: 640px) and (max-width: 1200px) {
  .hidden--tab {
    display: none !important;
  }
}
@media (min-width: 1200px) {
  .hidden--desktop {
    display: none !important;
  }
}

.section {
  width: 100%;
  max-width: 100%;
  min-width: 320px;
  margin: 0 auto;
  position: relative;
}
.section__ui {
  padding: 40px 0;
}
.section__ui input {
  margin-bottom: 20px;
}
.section__wrapper {
  max-width: var(--w-content);
  margin: 0 auto;
  padding: var(--c-padding);
  box-sizing: border-box;
  z-index: 2;
}
@media (max-width: 450px) {
  .section__wrapper {
    padding: 20px;
  }
}
@media (max-width: 640px) {
  .section__wrapper {
    padding: 20px;
  }
}
@media (max-width: 1200px) {
  .section__wrapper {
    padding: var(--c-padding);
  }
}
@media (max-width: 1500px) {
  .section__wrapper {
    padding: var(--c-padding);
  }
}
@media (min-width: 320px) {
  .section__wrapper {
    width: 100%;
    padding: 20px;
  }
}
@media (max-width: 2560px) {
  .section__wrapper {
    width: 100%;
    padding: var(--c-padding);
  }
}
.section__header-mt {
  margin-top: var(--h-header);
}

.flex {
  display: flex;
}
.flex.wrap {
  flex-wrap: wrap;
}
.flex.row {
  flex-direction: row;
}
.flex.col {
  flex-direction: column;
}
.flex > * {
  flex: 1 1 auto;
  margin-top: 0;
}

.media {
  display: flex;
  overflow: hidden;
  position: relative;
}
.media--bg {
  background: var(--bg-light);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.media--bg img, .media--bg video, .media--bg iframe {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.media--img {
  background: var(--bg-light-2);
}

.align-right {
  display: flex;
  justify-content: "flex-end";
  align-items: center;
}

@media (min-width: 640px) {
  .mobile-only {
    display: none !important;
  }
}

@media (min-width: 1200px) {
  .mobile-plus-tablet {
    display: none !important;
  }
}

@media (max-width: 640px) {
  .desktop-plus-tablet {
    display: none !important;
  }
}

.button {
  position: relative;
  display: flex;
  align-content: center;
  font-weight: 400;
  font-size: 13px;
  line-height: 120%;
  text-decoration: none;
  border-radius: 16px;
  padding: 8px 10px 8px 10px;
  width: auto;
  outline: none;
  border: none;
  cursor: pointer;
  transition: all linear 0.2s;
}
.button.disabled, .button[disabled] {
  pointer-events: none;
  opacity: 0.25;
}
.button--white {
  border-width: 1px;
  border-style: solid;
  border-color: var(--white);
  color: var(--white);
  background: rgba(0, 0, 0, 0);
}
.button--white:hover {
  color: var(--yellow-main);
  border: 1px solid var(--yellow-main);
}
.button--yellow {
  color: var(--black);
  background: var(--yellow-secondary);
}
.button--yellow:hover {
  background: var(--yellow-main);
}
.button--big {
  border-radius: 24px;
  padding: 15px 24px 15px 24px;
}

.language-link {
  position: relative;
  display: inline-flex;
  align-items: center;
  margin-right: auto;
  cursor: pointer;
  text-decoration: none;
  min-width: 40px;
  font-weight: 400;
  font-size: 13px;
  line-height: 120%;
  color: var(--white);
}
.language-link:hover {
  color: var(--yellow-main);
}
.language-link__icon {
  display: inline-block;
  margin-left: 12px;
  width: 6px;
  height: 4px;
  background: var(--yellow-secondary);
  mask-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='6' height='4' viewBox='0 0 6 4' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M3.32822 3.46667C3.12822 3.73333 2.72822 3.73333 2.52822 3.46667L0.528223 0.799999C0.281009 0.470381 0.5162 -5.32739e-07 0.928223 -4.96719e-07L4.92822 -1.47027e-07C5.34025 -1.11007e-07 5.57544 0.470382 5.32822 0.8L3.32822 3.46667Z' fill='%23FFDF65'/%3e%3c/svg%3e");
  mask-size: contain;
  mask-repeat: no-repeat;
  transition: transform 0.3s ease;
}
.language-link__icon.is-active {
  transform: rotate(90deg);
}
.language-link__dropdown {
  display: none;
  position: absolute;
  background: var(--pro-lines-bg-pro);
  list-style: none;
  margin: 0 0 0 -10px;
  padding: 5px 10px;
  border-radius: 0 0 4px 4px;
  z-index: 3;
}
.language-link__dropdown a {
  color: var(--white);
}
.language-link__dropdown a:hover {
  color: var(--yellow-secondary);
}

.promo-links {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  width: 100%;
  gap: 16px;
  margin-top: 32px;
}
.promo-links__item {
  display: flex;
  justify-content: "flex-start";
  align-items: "flex-start";
  flex-direction: column;
}
.promo-links__item h3 {
  font-weight: 600;
  font-size: 13px;
  line-height: 140%;
  color: var(--gray);
  margin-bottom: 8px;
}
.promo-links__item ul {
  list-style: none;
}
.promo-links__item ul li {
  margin-bottom: 8px;
}
.promo-links__item ul li a {
  display: flex;
  justify-content: "flex-start";
  align-items: "center";
  color: var(--white);
}
.promo-links__item ul li a:hover {
  color: var(--yellow-secondary);
}
.promo-links__item ul li.current-menu-item a {
  color: var(--yellow-main);
}
.promo-links__mt {
  margin-top: 16px;
}
.promo-links__directions ul li a, .promo-links__catalog ul li a, .promo-links__company ul li a {
  font-weight: 400;
  font-size: 13px;
  line-height: 140%;
}
.promo-links__social {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
}
.promo-links__social a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: auto;
}
.promo-links__social a svg {
  width: 16px;
  height: 16px;
  min-width: 100%;
  fill: var(--yellow-secondary);
  transition: all ease-in-out 0.2s;
}
.promo-links__social a:hover svg {
  fill: var(--white);
}

.tag {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 18px;
  border-width: 1px;
  border-style: solid;
  border-radius: 4px;
  padding: 0 8px;
  margin-left: 4px;
  font-weight: 600;
  font-size: 9px;
  line-height: 120%;
}
.tag--discount {
  color: var(--white);
  border-color: var(--orange-2);
  background: var(--orange-2);
}
.tag--gift {
  color: var(--orange);
  border-color: var(--orange);
}
.tag--soon {
  color: var(--violet);
  border-color: var(--violet);
}
.tag--new {
  color: var(--green);
  border-color: var(--green);
}

h1, h2,
.h1h2 {
  font-size: var(--desctop-h1h2-font-size);
  line-height: var(--desctop-h1h2-line-height);
  font-weight: var(--desctop-h1h2-font-weight);
}

h3,
.h3 {
  font-size: var(--desctop-h3-font-size);
  line-height: var(--desctop-h3-line-height);
  font-weight: var(--desctop-h3-font-weight);
}

.body-s-sb, textarea, input {
  font-size: var(--desctop-body-s-sb-font-size);
  line-height: var(--desctop-body-s-sb-line-height);
  font-weight: var(--desctop-body-s-sb-font-weight);
}

.body-s-reg {
  font-size: var(--desctop-body-s-reg-font-size);
  line-height: var(--desctop-body-s-reg-line-height);
  font-weight: var(--desctop-body-s-reg-font-weight);
}

.body-m-sb {
  font-size: var(--desctop-body-m-sb-font-size);
  line-height: var(--desctop-body-m-sb-line-height);
  font-weight: var(--desctop-body-m-sb-font-weight);
}

.body-l-sb {
  font-size: var(--desctop-body-l-sb-font-size);
  line-height: var(--desctop-body-l-sb-line-height);
  font-weight: var(--desctop-body-l-sb-font-weight);
}

.cta-s {
  font-size: var(--desctop-cta-s-font-size);
  line-height: var(--desctop-cta-s-line-height);
  font-weight: var(--desctop-cta-s-font-weight);
}

.cta-s-link {
  font-size: var(--desctop-cta-s-link-font-size);
  line-height: var(--desctop-cta-s-link-line-height);
  font-weight: var(--desctop-cta-s-link-font-weight);
}

.sta-m, .button--big {
  font-size: var(--desctop-sta-m-font-size);
  line-height: var(--desctop-sta-m-line-height);
  font-weight: var(--desctop-sta-m-font-weight);
}

.cta-m-link {
  font-size: var(--desctop-cta-m-link-font-size);
  line-height: var(--desctop-cta-m-link-line-height);
  font-weight: var(--desctop-cta-m-link-font-weight);
}

.textaccedent {
  font-size: var(--desctop-textaccedent-font-size);
  line-height: var(--desctop-textaccedent-line-height);
  font-weight: var(--desctop-textaccedent-font-weight);
}

.list {
  margin-left: 30px;
}
.list li {
  font-size: var(--desctop-list-font-size);
  line-height: var(--desctop-list-line-height);
  font-weight: var(--desctop-list-font-weight);
}

.inp-wrp .error {
  font-weight: 400;
  font-size: 10px;
  line-height: 190%;
  color: var(--error);
}

.form-description {
  text-align: left;
  font-size: 10px;
  line-height: 140%;
  font-weight: 400;
  color: var(--black);
}
.form-description a {
  color: var(--black);
  text-decoration: underline;
}
.form-description a:hover {
  text-decoration: none;
}

textarea, input {
  color: var(--black);
  border: none;
  outline: none;
  font-family: var(--font-family-b);
  caret-color: var(--yellow-main);
  background-color: transparent;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: var(--gray-3);
}
textarea::-webkit-input-placeholder, input::-webkit-input-placeholder {
  color: var(--gray);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea:-moz-placeholder, input:-moz-placeholder {
  color: var(--gray);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea::-moz-placeholder, input::-moz-placeholder {
  color: var(--gray);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea:-ms-input-placeholder, input:-ms-input-placeholder {
  color: var(--gray);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea:focus, input:focus {
  color: var(--black);
  border-bottom-color: var(--gray-3);
}
textarea.required, input.required {
  color: var(--error);
  border-bottom-color: var(--error);
}
textarea.required::-webkit-input-placeholder, input.required::-webkit-input-placeholder {
  color: var(--error);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea.required:-moz-placeholder, input.required:-moz-placeholder {
  color: var(--error);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea.required::-moz-placeholder, input.required::-moz-placeholder {
  color: var(--error);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea.required:-ms-input-placeholder, input.required:-ms-input-placeholder {
  color: var(--error);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea:disabled, input:disabled, textarea:read-only, input:read-only {
  border-bottom-color: rgba(212, 212, 212, 0.32);
  cursor: default;
}
textarea:disabled::-webkit-input-placeholder, input:disabled::-webkit-input-placeholder, textarea:read-only::-webkit-input-placeholder, input:read-only::-webkit-input-placeholder {
  color: rgba(116, 116, 115, 0.32);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea:disabled:-moz-placeholder, input:disabled:-moz-placeholder, textarea:read-only:-moz-placeholder, input:read-only:-moz-placeholder {
  color: rgba(116, 116, 115, 0.32);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea:disabled::-moz-placeholder, input:disabled::-moz-placeholder, textarea:read-only::-moz-placeholder, input:read-only::-moz-placeholder {
  color: rgba(116, 116, 115, 0.32);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea:disabled:-ms-input-placeholder, input:disabled:-ms-input-placeholder, textarea:read-only:-ms-input-placeholder, input:read-only:-ms-input-placeholder {
  color: rgba(116, 116, 115, 0.32);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}

input {
  width: 100%;
  padding-bottom: 8px;
}
input[type=checkbox] {
  flex: 0 0 auto;
  width: 16px;
  height: 16px;
  border: 1px solid var(--gray-3);
  box-sizing: border-box;
  border-radius: 2px;
  outline: none;
  appearance: none;
  margin: 0 9px 0 0;
  padding: 0;
}
input[type=checkbox]:checked {
  background-image: url("data:image/svg+xml,%3Csvg width='10' height='9' viewBox='0 0 10 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M3.1687 7.72766C3.1687 7.72766 0.509972 4.76001 0.318855 4.54718C-0.106285 4.07284 -0.106285 3.30095 0.318855 2.82701C0.745078 2.35146 1.43437 2.35146 1.86059 2.82701L3.58184 4.74747C3.79587 4.98627 4.14322 4.98462 4.35545 4.74462L8.13478 0.467095C8.55955 -0.0137246 9.25357 -0.0169792 9.68196 0.461022C10.1038 0.93171 10.1064 1.70038 9.6874 2.17472C5.58324 6.82074 4.80383 7.72726 4.80383 7.72726C4.35325 8.22995 3.61964 8.23039 3.1687 7.72766Z' fill='%23FFCB05'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
  background-position: center 3px;
}

.form-radio {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 8px;
  width: 100%;
}
.form-radio__item {
  display: flex;
  justify-content: "flex-start";
  align-items: center;
  gap: 8px;
  box-sizing: border-box;
  cursor: pointer;
  position: relative;
}
.form-radio__item input[type=radio] {
  display: none;
}
.form-radio__item span {
  display: flex;
  justify-content: "flex-start";
  align-items: center;
  width: 100%;
  border-width: 1px;
  border-style: solid;
  border-radius: 4px;
  padding: 23px 16px;
  height: 64px;
  font-size: 13px;
  font-weight: 400;
  color: var(--gray-3);
  border-color: var(--gray-3);
